.contact-us {
  &__info {
    margin: 0 0 24px ;

    &--last {
      margin-bottom: 32px;
    }
  }

  &__info-title {
    margin: 0 0 8px;

    font-size: $fontSizeInfoTitle;
    line-height: $lineHeightInfoTitle;
    color: #9fadc2;
  }

  &__info-link {
    color: inherit;
    text-decoration: none;
  }

  &__field {
    margin: 0 0 16px;
  }

  &--last {
    margin-bottom: 32px;
  }
}
